export default {
	data() {
		return {
			count: 0
		};
	},
	props: {
		items:{
			type: Object,
			default: () => {}
		}
	},
	created() {
		let that = this
		uni.getStorage({
			key: "goodsList",
			success: (res => {
				res.data.forEach(function(item){
					if(item.food_id == that.items.specfoods[0].food_id) {
						that.count = item.foot_count
						console.log(that.count)
					}
				})
				
			})
		})
	},
	computed: {
	},
	methods: {
		addCart() {
			this.count++;
			this.$emit('add', this.count);
		},
		decreaseCart() {
			if (this.count) {
				this.count--;
			}
			this.$emit('sub', this.count);
		}
	}
};
